<template><section>

<h1>动画组件</h1>
<p><code class="code_inline">xl-vision</code>内置了多种动画效果，可以直接使用</p>
<h2 id="collapse-transition"><a class="header-anchor" href="#collapse-transition" aria-hidden="true">¶</a> Collapse Transition</h2>
<p>折叠组件，可以参看<code class="code_inline">collapse</code>组件的动画效果</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-item2 demo-item3\&quot; @click=\&quot;click1\&quot;&gt;点击{{show1?'隐藏':'展开'}}&lt;/div&gt;\n&lt;xl-collapse-transition&gt;\n  &lt;div v-show=\&quot;show1\&quot; class=\&quot;collapse1\&quot;&gt;\n    &lt;div class=\&quot;demo-item demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item2\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item2\&quot;&gt;&lt;/div&gt;\n  &lt;/div&gt;\n&lt;/xl-collapse-transition&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        show1: false\n      }\n    },\n    methods: {\n      click1() {\n        this.show1 = !this.show1\n      }\n    }\n  }\n&quot;,&quot;style&quot;:&quot;\n  .demo-item{\n    height: 4em;\n  }\n  .demo-item1{\n    background-color: #409eff;\n  }\n  .demo-item2{\n    background-color: #67c23a;\n  }\n  .demo-item3{\n    color: #fff;\n    font-size: 1.5em;\n    text-align: center;\n    line-height: 4em;\n    cursor: pointer;\n    font-weight: bold;\n  }\n  .collapse1.collapse-transition{\n    transition: all 2s ease-in-out;\n  }\n&quot;}">
                            <div><div class="demo-item2 demo-item3" @click="click1">点击{{show1?'隐藏':'展开'}}</div>
<xl-collapse-transition>
  <div v-show="show1" class="collapse1">
    <div class="demo-item demo-item1"></div>
    <div class="demo-item demo-item2"></div>
    <div class="demo-item demo-item1"></div>
    <div class="demo-item demo-item2"></div>
  </div>
</xl-collapse-transition>



</div>
                            <div slot="desc"><p>折叠组件,可以指定class <code>collapse-transition</code>来自定义动画效果，通常内容是<code>transition: all 2s ease-in-out;</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item2 demo-item3'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"click1"</span>&gt;</span>点击{{show1?'隐藏':'展开'}}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-collapse-transition</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"show1"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse1"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-collapse-transition</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.demo-item</span>{
    <span class="hljs-attribute">height</span>: <span class="hljs-number">4em</span>;
  }
  <span class="hljs-selector-class">.demo-item1</span>{
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#409eff</span>;
  }
  <span class="hljs-selector-class">.demo-item2</span>{
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#67c23a</span>;
  }
  <span class="hljs-selector-class">.demo-item3</span>{
    <span class="hljs-attribute">color</span>: <span class="hljs-number">#fff</span>;
    <span class="hljs-attribute">font-size</span>: <span class="hljs-number">1.5em</span>;
    <span class="hljs-attribute">text-align</span>: center;
    <span class="hljs-attribute">line-height</span>: <span class="hljs-number">4em</span>;
    <span class="hljs-attribute">cursor</span>: pointer;
    <span class="hljs-attribute">font-weight</span>: bold;
  }
  <span class="hljs-selector-class">.collapse1</span><span class="hljs-selector-class">.collapse-transition</span>{
    <span class="hljs-attribute">transition</span>: all <span class="hljs-number">2s</span> ease-in-out;
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show1</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      click1() {
        <span class="hljs-keyword">this</span>.show1 = !<span class="hljs-keyword">this</span>.show1
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="slide-transition"><a class="header-anchor" href="#slide-transition" aria-hidden="true">¶</a> Slide Transition</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-row&gt;\n  &lt;xl-col :span=\&quot;6\&quot;&gt;\n    &lt;xl-radio-group type=\&quot;button\&quot; v-model=\&quot;direction\&quot;&gt;\n      &lt;xl-radio label=\&quot;top\&quot;&gt;&lt;/xl-radio&gt;\n      &lt;xl-radio label=\&quot;bottom\&quot;&gt;&lt;/xl-radio&gt;\n      &lt;xl-radio label=\&quot;left\&quot;&gt;&lt;/xl-radio&gt;\n      &lt;xl-radio label=\&quot;right\&quot;&gt;&lt;/xl-radio&gt;\n    &lt;/xl-radio-group&gt;\n  &lt;/xl-col&gt;\n  &lt;xl-col :span=\&quot;12\&quot;&gt;\n    &lt;xl-button @on-click=\&quot;click2\&quot;&gt;点击向{{direction==='top'?'上':direction==='bottom'?'下':direction==='left'?'左':'右'}}{{show2?'隐藏':'展开'}}&lt;/xl-button&gt;\n  &lt;/xl-col&gt;\n&lt;/xl-row&gt;\n&lt;transition :name=\&quot;'xl-slide-'+direction\&quot;&gt;\n  &lt;div v-show=\&quot;show2\&quot;&gt;\n    &lt;div class=\&quot;demo-item demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item2\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item demo-item2\&quot;&gt;&lt;/div&gt;\n  &lt;/div&gt;\n&lt;/transition&gt;\n\n\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        direction: 'top',\n        show2: false\n      }\n    },\n    methods: {\n      click2() {\n        this.show2 = !this.show2\n      }\n    }\n  }\n&quot;,&quot;style&quot;:&quot;\n  .demo-item{\n    height: 4em;\n  }\n  .demo-item1{\n    background-color: #409eff;\n  }\n  .demo-item2{\n    background-color: #67c23a;\n  }\n  .demo-item3{\n    color: #fff;\n    font-size: 1.5em;\n    text-align: center;\n    line-height: 4em;\n    cursor: pointer;\n    font-weight: bold;\n  }\n&quot;}">
                            <div><xl-row>
  <xl-col :span="6">
    <xl-radio-group type="button" v-model="direction">
      <xl-radio label="top"></xl-radio>
      <xl-radio label="bottom"></xl-radio>
      <xl-radio label="left"></xl-radio>
      <xl-radio label="right"></xl-radio>
    </xl-radio-group>
  </xl-col>
  <xl-col :span="12">
    <xl-button @on-click="click2">点击向{{direction==='top'?'上':direction==='bottom'?'下':direction==='left'?'左':'右'}}{{show2?'隐藏':'展开'}}</xl-button>
  </xl-col>
</xl-row>
<transition :name="'xl-slide-'+direction">
  <div v-show="show2">
    <div class="demo-item demo-item1"></div>
    <div class="demo-item demo-item2"></div>
    <div class="demo-item demo-item1"></div>
    <div class="demo-item demo-item2"></div>
  </div>
</transition>





</div>
                            <div slot="desc"><p>使用<code>xl-slide-*</code>实现不同方向的滑动效果</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"6"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio-group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"direction"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"top"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"bottom"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"left"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">xl-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"right"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-radio-group</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"12"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">@on-click</span>=<span class="hljs-string">"click2"</span>&gt;</span>点击向{{direction==='top'?'上':direction==='bottom'?'下':direction==='left'?'左':'右'}}{{show2?'隐藏':'展开'}}<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-row</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">transition</span> <span class="hljs-attr">:name</span>=<span class="hljs-string">"'xl-slide-'+direction"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"show2"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">transition</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">direction</span>: <span class="hljs-string">'top'</span>,
        <span class="hljs-attr">show2</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      click2() {
        <span class="hljs-keyword">this</span>.show2 = !<span class="hljs-keyword">this</span>.show2
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.demo-item</span>{
    <span class="hljs-attribute">height</span>: <span class="hljs-number">4em</span>;
  }
  <span class="hljs-selector-class">.demo-item1</span>{
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#409eff</span>;
  }
  <span class="hljs-selector-class">.demo-item2</span>{
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#67c23a</span>;
  }
  <span class="hljs-selector-class">.demo-item3</span>{
    <span class="hljs-attribute">color</span>: <span class="hljs-number">#fff</span>;
    <span class="hljs-attribute">font-size</span>: <span class="hljs-number">1.5em</span>;
    <span class="hljs-attribute">text-align</span>: center;
    <span class="hljs-attribute">line-height</span>: <span class="hljs-number">4em</span>;
    <span class="hljs-attribute">cursor</span>: pointer;
    <span class="hljs-attribute">font-weight</span>: bold;
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
</div></demo-block>
</section></template>
<style>
  .demo-item{
    height: 4em;
  }
  .demo-item1{
    background-color: #409eff;
  }
  .demo-item2{
    background-color: #67c23a;
  }
  .demo-item3{
    color: #fff;
    font-size: 1.5em;
    text-align: center;
    line-height: 4em;
    cursor: pointer;
    font-weight: bold;
  }
  .collapse1.collapse-transition{
    transition: all 2s ease-in-out !important;
  }
</style>
<script>
  export default {
    data() {
      return {
        show1: false,
        direction: 'top',
        show2: false
      }
    },
    methods: {
      click1() {
        this.show1 = !this.show1
      },
      click2() {
        this.show2 = !this.show2
      }
    }
  }
</script>


